@import 'scss/variables/index';

.pd-products {
  background: $primary-background-color;
  min-height: 100vh;
  padding: $spacing-xl 0;

  img {
    border-radius: $spacing-xs;
    margin: $spacing-lg 0;
    width: 100%;
  }

  &-divider {
    border-right: 1px solid darkgray;
    height: 360px;
    width: 1px;
  }

  &-header {
    color: $wilco-primary;
    font-size: 60px;
  }

  &-title {
    color: $wilco-primary;
    font-size: 24px;
    font-weight: bold;
  }

  &-description {
    color: $wilco-primary;

    .specs {
      padding: 5px 15px;
    }
  }

  &-extras {
    .flex-container {
      display: flex;
    }
  }

  .ant-divider-vertical {
    background: $pd-color-gray;
  }

  .ant-carousel {
    .slick-slide {
      background: $pd-color-white;
      height: 400px;
      line-height: 400px;
      overflow: hidden;
      text-align: center;
    }

    .slick-dots {
      li {
        button {
          background: $secondary-color;
        }

        &.slick-active {
          button {
            background: $primary-color;
          }
        }
      }
    }
  }
}
